    line-height: 60px;
    height: 60px;
    padding-top: 0;
  }
  .navbar-default .nav > li:hover .dropdown-menu {
    visibility: visible;
    opacity: 1;
  }
  .navbar-default .nav > li .dropdown-menu {
    display: block;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all 0.35s;
    -moz-transition: all 0.35s;
